
import { useState } from 'react'
import './App.css';
import { pitchMap, toScale } from './constants/pitchMap'
import { scales } from './constants/scale'
import { tone, transCode, scaleNumbers } from './constants/tone'

const keys = Object.keys(tone)

const renderCode = code => {
  const s = code.split('/')
  if (s[1]) {
    return (
      <div className='code_container'>
        <div className='code_txt'>{s[0]}</div>
        <div className='code_sub_txt' >{s[1]}</div>
      </div>
    )
  }
  return (
    <div className='code_txt'>{s[0]}</div>
  )
}

function App() {
  const [activeKey, setActiveKey] = useState('E')


  return (
    <div className="App">
      <div className='tone_header'>
        {
          keys.map(_ => {
            const key = tone[_]
            return (
              <div
                key={key}
                className={`tone_header_item ${activeKey === key ? 'tone_active' : ''}`}
                onClick={() => {
                  setActiveKey(key)
                }}
              >
                {transCode(key, 1)}
              </div>
            )
          })
        }
      </div>

      {/* 音阶 */}
      <div className='scales_container'>
        {
          scales.map((scale, i) => {
            const scaleCodes = toScale(scale.intervals, activeKey)
            return (
              <div
                key={i}
                className={`scale_container`}
              >
                <div className='scale_name'>
                  {scale['name']}
                </div>
                {scaleNumbers.map((n, i) => {
                  return (
                    <div
                      key={i}
                      className='scale_number no-select'>
                      {n}
                    </div>
                  )
                })}
                {/* 音阶 */}
                <div className='scale_code'>
                  {scale.intervals.map((_, i) => {
                    const code = scaleCodes[i]
                    return (
                      <div
                        key={i}
                        className='scale_code_txt'
                        style={{
                          left: (_ - 1) * 80,
                        }}>
                        {
                          renderCode(transCode(code,))
                        }
                      </div>
                    )
                  })}
                </div>
              </div>
            )
          })
        }
      </div>
    </div>
  );
}

export default App;
