export const tone = {
  1: 'C', 2: 'C#', 3: 'D', 4: 'D#', 5: 'E', 6: 'F', 7: 'F#', 8: 'G', 9: 'G#', 10: 'A', 11: 'A#', 12: 'B',
}

export const scaleNumbers = [
  '1',
  '2b',
  '2',
  '3b',
  '3',
  '4',
  '5b',
  '5',
  '6b',
  '6',
  '7b',
  '7',
]

export const transCode = (s, type) => {
  if (type === 1) {
    if (s === 'C#') return 'C#'
    if (s === 'D#') return 'Eb'
    if (s === 'F#') return 'F#'
    if (s === 'G#') return 'Ab'
    if (s === 'A#') return 'Bb'
    return s
  }
  if (type === 2) {
    if (s === 'C#') return 'Db'
    if (s === 'D#') return 'Eb'
    if (s === 'F#') return 'Gb'
    if (s === 'G#') return 'Ab'
    if (s === 'A#') return 'Bb'
    return s
  }
  if (s === 'C#') return 'C#/Db'
  if (s === 'D#') return 'Eb/D#'
  if (s === 'F#') return 'F#/Gb'
  if (s === 'G#') return 'Ab/G#'
  if (s === 'A#') return 'Bb/A#'
  return s
}
