export const pitchMap = {
  1: 'C', 2: 'C#', 3: 'D', 4: 'D#', 5: 'E', 6: 'F', 7: 'F#', 8: 'G', 9: 'G#', 10: 'A', 11: 'A#', 12: 'B',
  13: 'C', 14: 'C#', 15: 'D', 16: 'D#', 17: 'E', 18: 'F', 19: 'F#', 20: 'G', 21: 'G#', 22: 'A', 23: 'A#', 24: 'B',
  25: 'C', 26: 'C#', 27: 'D', 28: 'D#', 29: 'E', 30: 'F', 31: 'F#', 32: 'G', 33: 'G#', 34: 'A', 35: 'A#', 36: 'B',
  37: 'C', 38: 'C#', 39: 'D', 40: 'D#', 41: 'E', 42: 'F', 43: 'F#', 44: 'G', 45: 'G#', 46: 'A', 47: 'A#', 48: 'B',
  49: 'C', 50: 'C#', 51: 'D', 52: 'D#', 53: 'E', 54: 'F', 55: 'F#', 56: 'G', 57: 'G#', 58: 'A', 59: 'A#', 60: 'B',
  61: 'C', 62: 'C#', 63: 'D', 64: 'D#', 65: 'E', 66: 'F', 67: 'F#', 68: 'G', 69: 'G#', 70: 'A', 71: 'A#', 72: 'B',
  73: 'C', 74: 'C#', 75: 'D', 76: 'D#', 77: 'E', 78: 'F', 79: 'F#', 80: 'G', 81: 'G#', 82: 'A', 83: 'A#', 84: 'B',
  85: 'C', 86: 'C#', 87: 'D', 88: 'D#', 89: 'E', 90: 'F',
}


export const toScale = (intervals = [], startAt = 'C') => {
  const pitchs = ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B']
  const startIndex = pitchs.findIndex(_ => _ === startAt)
  const scaleIndexs = intervals.map(_ => _ + startIndex)
  return scaleIndexs.map(_ => {
    return pitchMap[_]
  })
}




